.content-container {
  position: relative;
  height: 100%;
  background-color: #fff;
}
.content-container .ant-tabs-tabpane {
  position: relative;
  height: calc(100vh - 104px);
  overflow: auto;
}
.content-container .bg-box {
  position: 'absolute';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #eee;
}
.content-container .bg-size {
  width: 100%;
  height: calc(100vh - 64px);
  overflow: hidden;
}
.content-container .tabpane-box {
  min-height: calc(100vh - 173px);
}
.my-sider {
  position: relative;
  z-index: 10;
  height: 100%;
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
}
.my-sider.light {
  box-shadow: 2px 0 8px 0 rgba(29, 35, 41, 0.05);
}
.my-sider .ant-menu-light {
  border-right-color: transparent;
}
.my-sider .sider-menu-logo {
  position: relative;
  height: 64px;
  padding-left: 24px;
  overflow: hidden;
  line-height: 64px;
  background: #001529;
  transition: all 0.3s;
}
.my-sider .sider-menu-logo.light {
  background: #fff;
  box-shadow: 1px 1px 0 0 #e8e8e8;
}
.my-sider .sider-menu-logo.light h1 {
  color: #1890ff;
}
.my-sider .sider-menu-logo img {
  display: inline-block;
  height: 32px;
  vertical-align: middle;
}
.my-sider .sider-menu-logo h1 {
  display: inline-block;
  margin: 0 0 0 12px;
  color: #fff;
  font-weight: 600;
  font-size: 20px;
  font-family: Avenir, Helvetica Neue, Arial, Helvetica, sans-serif;
  vertical-align: middle;
}
